*{
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;   
}

html, 
body, 
#potato,
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

/* className="loader */
.loader {
 width: 100%;
 height: 100%;
 display: flex;
 justify-content: center;
 align-items: center; 
 /* 가운데로 */
}

.movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;

}

.movies .movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
     
}
.movie img{
    max-width: 200px;
}

.movie h3,
.movie h5{
    font-weight: 300;
}